.dark-mode {
  background-color: rgb(40, 40, 40);
}
.dark-mode p,
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4 {
  color: white;
}
.dark-mode .anuncios,
.dark-mode .resumen-propiedad {
  background: rgb(83, 83, 83);
  border: 1px solid rgb(82, 82, 82);
}
.dark-mode .anuncios .precio,
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncios .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: white;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: rgb(224, 221, 221);
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: rgb(69, 69, 69);
  color: white;
  border: 1px rgb(39, 39, 39) solid;
}

/**
 * Componentes
 */
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-image: var(--header-bg-image, url(../img/header.jpg));
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 2.5rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
@media (min-width: 768px) {
  .footer .contenedor-footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer .navegacion {
    flex-direction: row;
    margin-bottom: 0;
    gap: 2rem;
  }
}
.footer .navegacion a {
  color: #FFFFFF;
}
.footer .info-empresa {
  color: #FFFFFF;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .footer .info-empresa {
    flex-direction: row;
    justify-content: flex-end;
    gap: 4rem;
    text-align: left;
  }
}
@media (min-width: 768px) {
  .footer .info-empresa .columna-datos {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .footer .info-empresa .columna-horario {
    text-align: left;
  }
}
.footer .info-empresa h4 {
  font-size: 1.8rem;
  margin: 0 0 1rem 0;
  font-weight: 700;
  color: #FFFFFF;
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 0.5rem;
  display: inline-block;
}
@media (min-width: 768px) {
  .footer .info-empresa h4 {
    margin-top: 0;
  }
}
.footer .info-empresa p {
  margin: 0.5rem 0;
  font-size: 1.4rem;
  line-height: 1.6;
  color: #e1e1e1;
}
.footer .info-empresa h5 {
  font-size: 1.6rem;
  margin: 0 0 1rem 0;
  font-weight: 700;
  color: #FFFFFF;
  text-transform: uppercase;
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 0.5rem;
  display: inline-block;
}
@media (min-width: 768px) {
  .footer .info-empresa h5 {
    margin-top: 0;
  }
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  margin-top: 3rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0rem;
    font-size: 1.8rem;
  }
}

