  margin-bottom: 1.5rem;
  color: #aeaeae;
  flex-grow: 1;
}
.anuncio img, .anuncios img {
  width: 100%;
  height: 30rem;
  object-fit: cover;
}
.anuncio .precio, .anuncios .precio {
  order: 1;
  margin-bottom: 1rem;
  margin-top: auto;
}
.anuncio .iconos-caracteristicas, .anuncios .iconos-caracteristicas {
  order: 2;
  margin-top: 0;
