.blog-sidebar h3 {
  margin-top: 0;
  margin-bottom: 2rem;
  text-align: center;
  color: #71B100;
  font-weight: 700;
  font-size: 2.4rem;
  padding-bottom: 0.8rem;
  border-bottom: 2px solid #71B100;
}
.blog-sidebar .categorias-blog {
  list-style: none;
  padding: 0;
  margin: 0;
}
.blog-sidebar .categorias-blog li {
  margin-bottom: 1.2rem;
  transition: transform 0.3s ease;
}
.blog-sidebar .categorias-blog li:hover {
  transform: translateY(-3px);
}
.blog-sidebar .categorias-blog li a {
  display: block;
  padding: 1.2rem;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  color: #000000;
  font-weight: 700;
  text-align: center;
  transition: all 0.3s ease;
  position: relative;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
.blog-sidebar .categorias-blog li a::before {
  content: "→";
  position: absolute;
  left: 1.5rem;
  opacity: 0;
  transition: opacity 0.3s ease, transform 0.3s ease;
}
.blog-sidebar .categorias-blog li a:hover {
  background-color: #71B100;
  color: #FFFFFF;
  padding-left: 3rem;
  border-color: #71B100;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}
.blog-sidebar .categorias-blog li a:hover::before {
  opacity: 1;
  transform: translateX(-5px);
}

.entrada-completa {
  margin-bottom: 3rem;
}
.entrada-completa h1 {
  margin: 2rem 0;
  font-size: 3.4rem;
}
.entrada-completa .meta-entrada {
  padding: 1rem 0;
  margin-bottom: 2rem;
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .entrada-completa .meta-entrada {
    flex-direction: row;
    justify-content: space-between;
  }
}
.entrada-completa .meta-entrada span {
  color: #71B100;
  font-weight: 700;
}
.entrada-completa .imagen-entrada {
  width: 100%;
  height: auto;
  max-height: 40rem;
  object-fit: cover;
  margin-bottom: 2rem;
}
.entrada-completa .contenido-entrada p {
  margin-bottom: 2rem;
  line-height: 1.8;
}
.entrada-completa .contenido-entrada ul, .entrada-completa .contenido-entrada ol {
  margin-bottom: 2rem;
  line-height: 1.8;
}
.entrada-completa .contenido-entrada h2, .entrada-completa .contenido-entrada h3, .entrada-completa .contenido-entrada h4 {
  margin: 2.5rem 0 1.5rem 0;
  color: #71B100;
}
.entrada-completa .contenido-entrada img {
  max-width: 100%;
  height: auto;
  margin: 2rem 0;
}

.admin-categorias-grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .admin-categorias-grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .admin-categorias-grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.categoria-card {
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.categoria-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.18);
}
.categoria-card__header {
  background-color: #333333;
  color: #FFFFFF;
  padding: 1rem 1.5rem;
  margin: 0;
}
.categoria-card__header h3 {
  margin: 0;
  font-size: 1.8rem;
  font-weight: 700;
}
.categoria-card__body {
  padding: 1.5rem;
  flex-grow: 1;
}
.categoria-card__body p {
  margin: 0 0 1rem 0;
  line-height: 1.6;
}
.categoria-card__body .sin-descripcion {
  color: #e1e1e1;
  font-style: italic;
}
.categoria-card__body .total-entradas {
  font-weight: 700;
  color: #333333;
  margin-top: 1.5rem;
  border-top: 1px solid #e1e1e1;
  padding-top: 1rem;
  font-size: 1.4rem;
}
.categoria-card__body .total-entradas span {
  font-size: 2rem;
  color: #71B100;
  font-weight: 900;
}
.categoria-card__footer {
  background-color: #FFFFFF;
  padding: 1rem 1.5rem;
  border-top: 1px solid #e1e1e1;
}
.categoria-card__footer .acciones {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}
.categoria-card__footer .acciones .boton {
  margin: 0;
  padding: 0.8rem 1.5rem;
  font-size: 1.3rem;
  flex: 1;
  text-align: center;
  transition: filter 0.3s ease;
}
.categoria-card__footer .acciones .boton:hover {
  filter: brightness(90%);
}
.categoria-card__footer .acciones .formulario-eliminar {
  flex: 1;
  display: flex;
}
.categoria-card__footer .acciones .formulario-eliminar .boton-rojo {
  width: 100%;
}

